body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  overflow-x: hidden;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.Portrait {
  height: 50vmin;
  border-radius: 10px;
  margin-bottom: 40px;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.link-item {
    display: flex;
    margin: 15px;
    padding-top: 3px;
    width: 44px;
    height: 44px;
    align-items: center;
    justify-content: center;
    border-radius: 999px;
}

.link-item:hover {
    background-color: #555;
    box-shadow: 0 0 10px 8px #555;
    transition: all 0.2s ease-in-out;
}

.link-item img {
    width: 40px;
}
.bio {
  display: flex;
  flex-direction: column;
  height: 100vh;
  width: 100vw;
  justify-content: center;
  align-items: center;
  color: white;
}

.bio-name {
  margin: 15px;
  font-size: 40px;
  font-weight: 400;
  color: #fff;
  text-shadow: black 0 2px 6px;
}

.bio-links {
  display: flex;
  flex-direction: row;
}

.portrait {
  margin: 0 0 25px;
  max-width: 250px;
  max-height: 250px;
  border-radius: 999px;
  box-shadow: 2px 2px 16px 0px #333;
}

.bio-table-contents {
  flex: 1 1;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  margin: 15px;
}

.bio-contents {
  flex: 999 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.contentLink {
  margin: 0 50px;
  font-size: 24px;
  font-weight: 450;
  color: #fff;
  text-decoration: none;
  text-shadow: black 1px 2px 6px;
}

.contentLink:visited {
  color: #fff;
}

.contentLink:hover,
.contentLink:active {
  color: #555;
}

@media only screen and (max-width: 800px) {
  .bio-name {
    font-size: 6.2vw;
  }
  .portrait {
      max-width: 30vw;
      max-height: 30vw;
  }

  .contentLink {
    font-size: 3vw;
    margin: 0 25px;
  }
}

@media only screen and (max-width: 500px) {
  .portrait {
      max-width: 45vw;
      max-height: 45vw;
  }

  .contentLink {
      font-size: 3vw;
      margin: 0 15px;
  }
}
.experience {
  display: flex;
  position: relative;
  flex-direction: column;
  height: 100vh;
  width: 100vw;
  justify-content: center;
  align-items: center;
  color: white;
}

.experience-title {
  flex: 1 1;
  margin: 15px;
  font-size: 40px;
  font-weight: 400;
  color: #fff;
  text-shadow: black 0 2px 6px;
}

.experience-content {
  flex: 999 1;
  display: flex;
  flex-direction: row;
}

@media only screen and (max-width: 750px) {
  .experience-content {
    flex-direction: column;
  }
}
.experience-item {
	display: flex;
	flex-direction: column;
  height: 68vh;
  width: 28vw;
  margin: 20px;
  align-items: center;
  border-radius: 14px;
	background-color: white;
  box-shadow: 2px 2px 16px 0px #333;
  overflow: hidden;
}

.experience-item img {
  display: block;
  height: 60px;
  width: auto;
  margin: 10px;
}

.exp-item-pos-date {
  display: flex;
  flex-direction: column;
	color: black;
}

.exp-item-pos-date p {
  margin: 2px;
}

#position {
  font-size: 18px;
  font-weight: bold;
}

.desc-list {
  display: table-cell;
  flex-direction: column;
  margin: 10px 8px;
  padding: 18px;
  text-align: left;
  vertical-align: center;
  overflow: hidden;
}

.desc-item {
  margin: 0 0 14px;
  color: black;
}

@media only screen and (max-width: 750px) {
  .experience-item {
    flex-direction: row;
    height: 20vh;
    width: 90vw;
  }

  .exp-item-pos-date {
    width: 100%;
    justify-content: center;
    align-items: center;
  }

  .experience-item img {
    height: 15vw;
  }

  .desc-list {
    display: none;
  }
}
.skills {
  position: relative;
  display: flex;
  flex-direction: column;
  height: 100vh;
  width: 100vw;
  justify-content: center;
  align-items: center;
  color: white;
}

.skills-title {
  flex: 1 1;
  margin: 15px;
  font-size: 40px;
  font-weight: 400;
  color: #fff;
  text-shadow: black 0 2px 6px;
}

.skills-content {
  flex: 999 1;
  display: flex;
  flex-direction: row;
}

@media only screen and (max-width: 750px) {
  .skills-content {
    flex-direction: column;
  }
}
.skill-item {
  display: flex;
  flex-direction: column;
  margin: 20px;
  align-items: center;
}

.skill-layer {
  margin: 18px;
  font-size: 18px;
  font-weight: bold;
}

.skill-list {
  display: flex;
  flex-direction: column;
  height: 48vh;
  width: 18vw;
  padding: 4px;
  border-radius: 14px;
  background-color: white;
  box-shadow: 2px 2px 16px 0px #333;
  overflow: hidden;
}

.specific-skill {
  font-size: 18px;
  font-weight: 360;
  margin: 11px;
  padding: 8px;
  border-radius: 999px;
  background-color: #ccc;
  box-shadow: 2px 2px 8px 0px #333;
  color: black;
}

@media only screen and (max-width: 750px) {
  .skill-item,
  .skill-list {
    flex-direction: row;
    height: 16vh;
    width: 90vw;
  }

  .skill-list {
    flex-wrap: wrap;
  }

  .specific-skill {
    height: 4vw;
  }
}

@media only screen and (max-width: 500px) {
  .specific-skill {
    font-size: 3vw;
    height: 4vw;
  }
}
.projects {
  position: relative;
  display: flex;
  flex-direction: column;
  height: 100vh;
  width: 100vw;
  justify-content: center;
  align-items: center;
  color: white;
}

.projects-title {
  flex: 1 1;
  margin: 15px;
  font-size: 40px;
  font-weight: 400;
  color: #fff;
  text-shadow: black 0 2px 6px;
}

.projects-content {
  flex: 999 1;
  margin: 20px;
  display: flex;
  flex-direction: row;
}

@media only screen and (max-width: 750px) {
  .projects-content {
    flex-direction: column;
  }
}
.project-item {
  display: flex;
  flex-direction: column;
  height: 60vh;
  width: 24vw;
  margin: 20px;
  align-items: center;
  border-radius: 14px;
  background-color: white;
  box-shadow: 2px 2px 16px 0px #333;
  overflow: hidden;
}

.project-item img {
  display: block;
  height: 100px;
  width: auto;
  margin: 10px;
}

.project-name {
  font-weight: 550;
  font-size: 20px;
}

.project-name,
.project-desc,
.project-tools {
  color: black;
  margin: 16px;
}

.project-tools {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

.project-tools p {
  font-weight: 450;
  padding: 8px;
  margin: 4px;
  background-color: #ddd;
  border-radius: 8px;
}

@media only screen and (max-width: 750px) {
  .project-item {
    flex-direction: row;
    height: 30vh;
    width: 90vw;
  }

  .project-item img {
    height: 12vw;
  }

  .desc-tools {
    flex-direction: row;
  }
}

@media only screen and (max-width: 500px) {
  .project-item p {
    font-size: 3vw;
  }
}
.photography {
  position: relative;
  display: flex;
  flex-direction: column;
  height: 100vh;
  width: 100vw;
  justify-content: center;
  align-items: center;
  color: white;
}

.photography-title {
  flex: 1 1;
  margin: 15px;
  font-size: 40px;
  font-weight: 400;
  color: #fff;
  text-shadow: black 0 2px 6px;
}

.photography-content {
  flex: 999 1;
  width: 60%;
  margin-top: 40px;
  bottom: 10px;
  justify-content: flex-end;
  align-items: center;
}
